* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  max-height: 1000vh;
  max-width: 600vw;
}

::-webkit-scrollbar {
  width: 10px;
  background-color: transparent;
}

::-webkit-scrollbar-thumb {
  background-color: #800000;
}
.wrapper {
    position: relative;
    display: flex;
    flex-direction: column;
    height: 500vh;
    width: 100%;
    background-color: #ffffffc9;
    z-index: 0;
    overflow: hidden;
}
#heading {
    align-self: flex-start;
    position: relative;
    display: flex;
    justify-content: center;
    top: 0px;
    height: 100vh;
    width: 100%;
    background-color: #000000ad;
    overflow: hidden;
}

#start_box {
    display: flex;
    justify-content: center;
    position: absolute;
    height: 15vh;
    width: 100vw;
    background-size: 45%;
    overflow: hidden;
    z-index: 2;
}

#end_box {
    display: flex;
    justify-content: center;
    position: absolute;
    bottom: 0px;
    height: 15vh;
    width: 100%;
    /* background-image: linear-gradient(transparent, darkblue 150%); */
    z-index: 0;
}

img {
    position: absolute;
    height: 150%;
    width: auto;
    z-index: -1;
}

h1 {
    display: block;
    text-transform: uppercase;
    align-self: center;
    text-align: center;
    font-size: 20px;
    font-family: 'Merriweather', serif;
    color: white;
    z-index: -1;
}

ul {
    display: block;
    position: absolute;
    align-self: center;
    list-style: none;
    text-align: center;
    font-size: 46px;
    font-family: 'Fjalla One', sans-serif;
    -webkit-animation: showing_list 2s linear;
            animation: showing_list 2s linear;
    z-index: 1;
}

@-webkit-keyframes showing_list {

    0% {
        opacity: 0;
        /* top: 0%; */
    }

    100% {
        opacity: 1;
        /* top: 40%; */
    }

}

@keyframes showing_list {

    0% {
        opacity: 0;
        /* top: 0%; */
    }

    100% {
        opacity: 1;
        /* top: 40%; */
    }

}

a {
    display: block;
    text-transform: uppercase;
    text-decoration: none;
    color: white;
    transition: .5s;
}

a:hover {
    font-size: 52px;
}

a:active {
    font-size: 52px;
}

p {
    display: block;
    position: absolute;
    align-self: center;
    margin: 28px;
    font-size: 48px;
    font-family: 'Lobster', cursive;
    color: white;
}

p:hover {
    cursor: default;
}

