@import url('https://fonts.googleapis.com/css2?family=Fjalla+One&family=Lobster&family=Merriweather&display=swap');

#heading {
    align-self: flex-start;
    position: relative;
    display: flex;
    justify-content: center;
    top: 0px;
    height: 100vh;
    width: 100%;
    background-color: #000000ad;
    overflow: hidden;
}

#start_box {
    display: flex;
    justify-content: center;
    position: absolute;
    height: 15vh;
    width: 100vw;
    background-size: 45%;
    overflow: hidden;
    z-index: 2;
}

#end_box {
    display: flex;
    justify-content: center;
    position: absolute;
    bottom: 0px;
    height: 15vh;
    width: 100%;
    /* background-image: linear-gradient(transparent, darkblue 150%); */
    z-index: 0;
}

img {
    position: absolute;
    height: 150%;
    width: auto;
    z-index: -1;
}

h1 {
    display: block;
    text-transform: uppercase;
    align-self: center;
    text-align: center;
    font-size: 20px;
    font-family: 'Merriweather', serif;
    color: white;
    z-index: -1;
}

ul {
    display: block;
    position: absolute;
    align-self: center;
    list-style: none;
    text-align: center;
    font-size: 46px;
    font-family: 'Fjalla One', sans-serif;
    animation: showing_list 2s linear;
    z-index: 1;
}

@keyframes showing_list {

    0% {
        opacity: 0;
        /* top: 0%; */
    }

    100% {
        opacity: 1;
        /* top: 40%; */
    }

}

a {
    display: block;
    text-transform: uppercase;
    text-decoration: none;
    color: white;
    transition: .5s;
}

a:hover {
    font-size: 52px;
}

a:active {
    font-size: 52px;
}

p {
    display: block;
    position: absolute;
    align-self: center;
    margin: 28px;
    font-size: 48px;
    font-family: 'Lobster', cursive;
    color: white;
}

p:hover {
    cursor: default;
}

/* media querie's */

@media (min-width:240px) and (max-width:241px) {

    h1 {
        font-size: 12px;
    }

    ul {
        font-size: 12px;
    }

    a:hover {
        font-size: 16px;
    }

    p {
        font-size: 32px;
    }
    
}

@media (min-width: 100px) {

    h1 {
        font-size: 5.5vw;
    }

    ul {
        font-size: 8vw;
    }

    a:hover {
        font-size: 12vw;
    }

    p {
        font-size: 15vw;
    }

}

@media (min-width: 480px) {

    h1 {
        font-size: 35px;
    }

    p {
        font-size: 84px;
    }

    ul {
        font-size: 50px;
    }

    a:active {
        font-size: 60px;
    }

    a:hover {
        font-size: 60px;
    }

    img {
        height: 100%;
    }

}

@media (min-width: 580px) {

    #start_box {
        width: 100%;
    }

    h1 {
        font-size: 25px;
    }

    p {
        font-size: 50px;
    }

    ul {
        font-size: 50px;
    }

    a:active {
        font-size: 60px;
    }

    a:hover {
        font-size: 60px;
    }

    img {
        height: 100%;
    }

}

@media (min-width:600px) and (max-width:768px) {
    img {
        height: 160%;
    }
}

@media (min-width:769px) {

    img {
        height: 180%;
    }

}

@media (min-width:1450px) {

    h1 {
        font-size: 4vw;
    }

    ul {
        font-size: 4vw;
    }

    a:hover {
        font-size: 4.5vw;
    }

    p {
        font-size: 5vw;
    }

}